import { BrowserRouter, Routes, Route } from "react-router-dom";
import { fadeInLeft, fadeIn, fadeInDown, zoomIn } from "react-animations";
import Radium from "radium";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import Navbar from "./components/Navbar";
import Home from "./components/Home";
import ACM from "./components/ACM";
import AWS from "./components/AWS";
import EDC from "./components/EDC";
import GDSC from "./components/GDSC";

function App() {
  const zoom = {
    animation: "1s",
    animationName: Radium.keyframes(zoomIn),
  };

  const fade = {
    animation: "1s",
    animationName: Radium.keyframes(fadeIn),
  };

  const fadeLeft = {
    animation: "1s",
    animationName: Radium.keyframes(fadeInLeft),
  };

  const fadeDown = {
    animation: "1s",
    animationName: Radium.keyframes(fadeInDown),
  };

  return (
    <BrowserRouter>
      <Navbar />
      <Routes>
        <Route path="/" element={<Home zoom={zoom} />} />
        <Route path="/acm" element={<ACM fade={fade} fadeLeft={fadeLeft} />} />
        <Route path="/edc" element={<EDC fade={fade} fadeLeft={fadeLeft} />} />
        <Route path="/aws" element={<AWS fade={fade} fadeDown={fadeDown} />} />
        <Route path="/gdsc" element={<GDSC fade={fade} fadeDown={fadeDown} />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
