import React from "react";
import { StyleRoot } from "radium";

export default function ACM(props) {
  return (
    <StyleRoot className="container-fluid rounded-3 p-4">
      <div className="row">
        <img
          className="col-2 w-25 h-25"
          src="https://www.mhssce.ac.in/images/ACM.jpg"
          alt=""
          style={props.fadeLeft}
        />
        <div className="col-8 mx-4" style={props.fade}>
          <h1 className="display-5 fw-bold">ACM</h1>
          <p className="fs-4">
            MHSSCOE-ACM is a student chapter of ACM(Association for Computing
            Machinery), the world's largest educational and scientific computing
            society. MHSSCOE-ACM was established in 2014 under the guidance of
            teachers and with dedication of the students of IT department. Since
            then, this committee aims to strengthen the students collective
            voice through strong leadership, promotion of the highest standards,
            and recognition of technical excellence. It is a team of students
            who strive to organize and manage various technical and
            extra-curricular events, workshops and competition. MHSSCOE-ACM
            gives the undergraduates a platform to work on their strengths and
            improve their weaknesses. The committee consists of various teams
            (like; technical, social, cultural, sports, design) which allow the
            students to explore their qualities and in turn help them to build a
            strong personal
          </p>
        </div>
      </div>
    </StyleRoot>
  );
}
