import React from 'react'
import { Link } from "react-router-dom";

export default function Navbar() {
    const click = (event) => {
        document.getElementsByClassName('active')[0].classList.remove('active')
        event.target.classList.add('active')
    }
    return (
        <nav className="navbar navbar-expand-lg bg-dark py-3">
            <div className="container-fluid justify-content-center">
                <Link className="btn btn-outline-light active mx-2" onClick={click} to="/">Home</Link>
                <Link className="btn btn-outline-primary mx-2" onClick={click} to="/ACM">ACM</Link>
                <Link className="btn btn-outline-success mx-2" onClick={click} to="/EDC">EDC</Link>
                <Link className="btn btn-outline-warning mx-2" onClick={click} to="/AWS">AWS</Link>
                <Link className="btn btn-outline-danger mx-2" onClick={click} to="/GDSC">GDSC</Link>
            </div>
        </nav>
    )
}
