import React from "react";
import { StyleRoot } from "radium";

export default function AWS(props) {
  return (
    <StyleRoot className="container-fluid rounded-3 p-5">
        <img
          className="col-2 w-25 h-25 my-4"
          src="https://source.oglethorpe.edu/files/2019/03/1500px_Academy_logo_HD-Amazon.png"
          alt=""
          style={props.fadeDown}
        />
          <p className="fs-4" style={props.fade}>
            Anjuman-I-Islams's M.H. Saboo Siddik College of Engineering
            announced its participation in AWS Academy, an Amazon Web Services
            (AWS) program that provides higher education institutions with a
            ready-to-teach cloud computing curriculum Beginning in the month of
            September, students can enroll in courses that will prepare them to
            pursue careers in the fast-growing cloud computing space and
            industry-recognized AWS Certifications.
            <br />
            Cloud is the digital wonderland of Unlimited Resources driven by
            your Imagination - MHSSCOE-AWS Academy
          </p>
    </StyleRoot>
  );
}
