import React from "react";
import { StyleRoot } from "radium";

export default function Home(props) {
  return (
    <StyleRoot className="container-fluid text-center p-4 mt-2">
      <img
        className="w-25 mx-5"
        src="https://www.mhssce.ac.in/images/ACM.jpg"
        alt=""
        style={props.zoom}
      />
      <img
        className="w-25 mx-5 mb-4"
        src="https://www.klnce.edu/Amenities/Images/EDC/EDClogo.jpg"
        alt=""
        style={props.zoom}
      />
      <img
        className="w-25 mx-5"
        src="https://source.oglethorpe.edu/files/2019/03/1500px_Academy_logo_HD-Amazon.png"
        alt=""
        style={props.zoom}
      />
      <img
        className="w-75 my-5"
        src="https://res.cloudinary.com/startup-grind/image/upload/dpr_2.0,fl_sanitize/v1/gcs/platform-data-dsc/contentbuilder/logo_dark_horizontal_097s7oa.svg"
        alt=""
        style={props.zoom}
      />
    </StyleRoot>
  );
}
